.i{
    display: flex;
    height: 100vh;
    
}
.i-left{
    flex: 1;
    display: flex;
    align-items: center;
    justify-content: center;
}
.i-right{
    flex: 1;
    position: relative;
}
.i-left-wrapper{
    padding: 50px;
    height: 30%;
    display: flex;
    flex-direction: column;
    justify-content: space-between;
}
.i-intro{
    font-size: 30px;
    font-weight: 300;
}
.i-scroll{
    width: 30px;
    height: 30px;
    position: absolute;
    bottom: 20px;
}
.i-name{
    font-size: 60px;
}
.i-title{
    height: 50px;
    overflow: hidden;
}
.i-title-wrapper{
    height: 100%;
    animation: move 8s ease-in-out infinite alternate;
}
@keyframes move{
    33%{
        transform: translateY(-50px);
    }
    66%{
        transform: translateY(-100px);
    }
    100%{
        transform: translateY(-150px);

    }
}
.i-title-item{
    height: 50px;
    font-size: 30px;
    font-weight: bold;
    color: #4b40ee;
    display: flex;
    align-items: center;
}
.i-img{
    width: 100%;
    height: 100%;
    object-fit: cover;
    position: absolute;
    margin-top: 40px;
}
.i-bg{
    clip-path: polygon(100% 0%, 100% 52%, 100% 100%, 25% 100%, 0% 50%, 25% 0%);
    background-color: #4b40ee;
    width: 100%;
    height: 100%;
    position: absolute;
    top: 0;
    right: 0;
}
@media screen and (max-width:480px){

    .i{
        flex-direction: column;
    }
    .i-left-wrapper{
        padding: 10px;
        align-items: center;
        text-align: center;
        justify-content: center;
        height: 100%;
    }
    .i-desc{
        display: none;
    }
}
@media screen and (max-width:680px){
    .i{
        flex-direction: column;
    }
    .i-left-wrapper{
        padding: 10px;
        align-items: center;
        text-align: center;
        justify-content: center;
        height: 100%;
    }
    .i-desc{
        display: none;
    }
    .i-title-wrapper{
        margin-left: 4rem;
    }
}
